import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import { focusElement } from './utils/element'
// import { getParent, focusElement } from './utils/element'
import utils from './utils'
// import { datepickerClass } from './style'

// let target = null
// document.addEventListener(
//   'mousedown',
//   (e) => {
//     // eslint-disable-next-line prefer-destructuring
//     target = e.target
//   },
//   true,
// )

class Text extends PureComponent {
  constructor(props) {
    super(props)
    this.handleBlur = this.handleBlur.bind(this)
    this.handleInput = this.handleInput.bind(this)
    this.handleFocus = this.handleFocus.bind(this)
    this.bindElement = this.bindElement.bind(this)
  }

  componentDidUpdate(prevProps) {
    if (
      prevProps.focus !== this.props.focus
      && this.props.focus
      && this.element
      && this.props.editIndex === this.props.index
    ) {
      focusElement.end(this.element)
    }
  }

  bindElement(el) {
    const { onTextSpanRef } = this.props
    this.element = el
    if (onTextSpanRef) onTextSpanRef(el)
  }

  handleBlur(e) {
    const { format, index, onChange, value } = this.props
    const txt = e.target.innerText
    // if (getParent(target, `.${datepickerClass('picker')}`)) return
    if (txt === value) return
    if (txt.trim().length === 0) {
      onChange(undefined, index)
    } else {
      const newValue = utils.toDateWithFormat(txt, format, undefined)
      // if translate fail, clear
      if (!newValue) {
        this.element.innerText = null
      }
      onChange(newValue, index)
    }
  }

  handleFocus(e) {
    const { onTextSpanRef } = this.props
    if (onTextSpanRef) onTextSpanRef(e.target)
  }

  handleInput(e) {
    if (e.keyCode === 13) {
      e.preventDefault()
      e.stopPropagation()
      this.element.blur()
      this.handleBlur(e)

      // must wait for handleBlur to finish executing
      Promise.resolve().then(() => {
        document.dispatchEvent(new Event('mousedown', { bubbles: true }))
      })
    }
  }

  render() {
    const { className, inputable, value, placeholder, disabled, focus, index, hoverDateText } = this.props
    if (!inputable || disabled || !focus || hoverDateText) {
      return (
        <span
          onClick={this.handleFocus}
          ref={this.bindElement}
          className={className}
          data-index={index}
        >
          {hoverDateText || value || placeholder}
        </span>
      )
    }

    return (
      <span
        ref={this.bindElement}
        placeholder={placeholder}
        data-index={index}
        contentEditable={inputable}
        onBlur={this.handleBlur}
        onFocus={this.handleFocus}
        onKeyDown={this.handleInput}
        className={className}
        dangerouslySetInnerHTML={{ __html: value }}
      />
    )
  }
}

Text.propTypes = {
  disabled: PropTypes.bool,
  className: PropTypes.string,
  format: PropTypes.oneOfType([PropTypes.string, PropTypes.func]),
  index: PropTypes.number,
  inputable: PropTypes.bool,
  onChange: PropTypes.func,
  placeholder: PropTypes.any,
  value: PropTypes.string,
  onTextSpanRef: PropTypes.func,
  focus: PropTypes.bool,
  // textSpan: PropTypes.array,
  editIndex: PropTypes.number,
  // changeEditIndex: PropTypes.func,
  hoverDateText: PropTypes.string,
}

Text.defaultProps = {
  value: '',
}

export default Text
