import inputStyle from './styles/input.less'
import styles from './styles/index.less'
import listStyle from './styles/list.less'
import hideableStyle from './styles/hideable.less'
import inputTitle from './styles/inputTitle.less'

const createClass = (s, prefix) => (...args) => {
  const arr = Array.from(args)
  return arr.filter(Boolean).map((item) => {
    const name = item === '_' ? prefix : `${prefix}-${item}`
    return s[name] || name
  }).join(' ')
}

export default createClass

export const datepickerClass = createClass(styles, 'datepicker')
export const inputClass = createClass(inputStyle, 'input')
export const listClass = createClass(listStyle, 'list')
export const hidableClass = createClass(hideableStyle, 'hidable')
export const inputTitleClass = createClass(inputTitle, 'input-title-box')
