import React, { Component } from 'react'
import PropTypes from 'prop-types'

const firstUpperCase = ([first, ...rest]) => first.toUpperCase() + rest.join('')

export default (Origin, { key, isArray = false } = {}) => {
  const value = key || 'value'
  const defaultValueKey = `default${firstUpperCase(value)}`

  const C = class extends Component {
    static propTypes = {
      onChange: PropTypes.func,
    }

    constructor(props) {
      super(props)
      this.state = {
        value: Object.prototype.hasOwnProperty.call(props, value) ? null : props[defaultValueKey],
      }
      this.handleChange = this.handleChange.bind(this)
      this.getValue = this.getValue.bind(this)
    }

    getValue() {
      const v = Object.prototype.hasOwnProperty.call(this.props, value)
        // eslint-disable-next-line react/destructuring-assignment
        ? this.props[value] : this.state.value
      if (isArray && !Array.isArray(v)) return [v]
      return v
    }

    handleChange(v, ...args) {
      const { onChange } = this.props
      if (onChange) {
        onChange(v, ...args)
      }
      if (!Object.prototype.hasOwnProperty.call(this.props, [value])) {
        this.setState({ value: v })
      }
    }

    render() {
      const { ...rest } = this.props
      const props = {
        ...rest,
        onChange: this.handleChange,
        [value]: this.getValue(),
      }
      return <Origin {...props} />
    }
  }
  return C
}
