import React from 'react'
import PropTypes from 'prop-types'
import immer from 'immer'
import { Button } from 'shineout'
import { PureComponent } from '../public/component'
import shallowEqual from '../utils/shallowEqual'
import utils from '../utils'
import Picker from './picker'
import { datepickerClass } from '../style'
import Quick from './quick'

class Range extends PureComponent {
  constructor(props) {
    super(props)

    let mode
    switch (props.type) {
      case 'month':
        mode = 'month'
        break
      case 'time':
        mode = 'time'
        break
      default:
        mode = 'day'
    }
    this.defaultMode = mode


    this.state = {
      rangeDate: props.value,
      mode: [mode, mode],
    }

    this.pickers = []

    this.handleChange = this.handleChange.bind(this)
    this.handleFirstChange = this.handleChange.bind(this, 0)
    this.handleSecondChange = this.handleChange.bind(this, 1)
    this.bindFirstPicker = this.bindPicker.bind(this, 0)
    this.bindSecondPicker = this.bindPicker.bind(this, 1)
    this.handleDisabledStart = this.handleDisabled.bind(this, 'start')
    this.handleDisabledEnd = this.handleDisabled.bind(this, 'end')
    this.changeDateSmart = this.changeDateSmart.bind(this)
    this.fillTime = this.fillTime.bind(this)
    this.handleQuick = this.handleQuick.bind(this)
    this.changeCurrent = this.changeCurrent.bind(this)
    this.handleModeChange = this.handleModeChange.bind(this)
    this.changeTimeMode = this.handleModeChange.bind(this, 'time', undefined)
    this.changeDayMode = this.handleModeChange.bind(this, 'day', undefined)
    this.changeFirstMode = (v) => { this.handleModeChange(v, 0) }
    this.changeSecondMode = (v) => { this.handleModeChange(v, 1) }

    if (props.addCloseEvents) {
      props.addCloseEvents(() => {
        this.handleModeChange(this.defaultMode)
        this.lastTime = null
      })
    }
  }

  componentDidUpdate(prevProps) {
    const { rangeDate } = this.state
    if (
      rangeDate.length !== 1

      && (!shallowEqual(prevProps.value, this.props.value)
      && !shallowEqual(this.state.rangeDate, this.props.value))
    ) {
      // eslint-disable-next-line
      this.setState({ rangeDate: this.props.value })
    }
  }

  onConfirm = () => {
    const { handleBlur } = this.props
    handleBlur()
    this.handleModeChange(this.defaultMode)
  }

  bindPicker(index, el) {
    this.pickers[index] = el
  }

  resetRange(rangeDate) {
    this.setState({ rangeDate })
  }

  // handleDayHover() {}

  changeDateSmart(rangeDate) {
    if (!rangeDate[0] || !rangeDate[1]) return
    const [s, e] = rangeDate
    const { range, defaultTime } = this.props
    if (typeof range === 'number') {
      if (utils.compareAsc(s, utils.addSeconds(e, -range)) < 0) rangeDate[1] = utils.addSeconds(s, range)
    }
    if (utils.compareAsc(s, e) > 0) {
      //  
      const temp = rangeDate[0]
      // eslint-disable-next-line prefer-destructuring
      rangeDate[0] = rangeDate[1]
      rangeDate[1] = temp
    }
    const lastTime = this.lastTime || []
    rangeDate[0] = utils.cloneTime(rangeDate[0], lastTime[0] || defaultTime[0], utils.TIME_FORMAT)
    rangeDate[1] = utils.cloneTime(rangeDate[1], lastTime[1] || defaultTime[1], utils.TIME_FORMAT)
    if (utils.compareAsc(rangeDate[0], rangeDate[1]) > 0) {
      // eslint-disable-next-line prefer-destructuring
      rangeDate[1] = utils.toDate(rangeDate[0])
    }
  }

  changeCurrent(current, areaType, index) {
    let c = current
    if (index === 0) c = [current, utils.addMonths(current, 1)]
    if (index === 1) c = [utils.addMonths(current, -1), current]
    const handleOnChangeParams = utils.handleOnChangeParams(areaType)
    this.props.onChange(...handleOnChangeParams(c))
  }

  // Be consistent with the parent onChange, expand first params: index
  handleChange(index, date, change, end, mode, isQuickSelect, areaType) {
    const { type, range, min, max, current: preCurrent } = this.props

    const handleOnChangeParams = utils.handleOnChangeParams(areaType)
    if (!change) {
      let thisCurrent = date
      if (index === 0) {
        thisCurrent = [date, preCurrent[1]]
        if (utils.compareMonth(thisCurrent[0], thisCurrent[1], -1) > 0) {
          thisCurrent = [date, utils.addMonths(date, 1)]
        }
      }
      if (index === 1) {
        thisCurrent = [preCurrent[0], date]
        if (utils.compareMonth(thisCurrent[0], thisCurrent[1], -1) > 0) {
          thisCurrent = [utils.addMonths(date, -1), date]
        }
      }
      this.props.onChange(...handleOnChangeParams(thisCurrent))
      return
    }

    if (areaType === 'time') {
      // eslint-disable-next-line react/no-access-state-in-setstate
      const result = immer(this.state.rangeDate, (rangeDate) => {
        rangeDate[index] = date
        const [s, e] = rangeDate
        if (range && utils.compareAsc(s, e) === 1) {
          rangeDate[1 - index] = date
        }
        if (typeof range === 'number' && utils.compareAsc(s, utils.addSeconds(e, -range)) < 0) {
          rangeDate[1 - index] = utils.addSeconds(date, range * (index === 0 ? 1 : -1))
        }
      })
      this.setState(
        { rangeDate: result },
        () => {
          this.props.onChange(...handleOnChangeParams(result, true))
        },
      )
      return
    }

    let rangeDate = [...this.state.rangeDate]
    let turn = rangeDate.filter(Boolean).length
    if (turn === 2) {
      this.lastTime = rangeDate.map(utils.toDate)
      rangeDate = []
      turn = 0
    }

    if (type === 'month') {
      // eslint-disable-next-line
      rangeDate[turn] = date
      rangeDate[1 - turn] = rangeDate[1 - turn] || ''

      this.changeDateSmart(rangeDate)

      this.setState({ rangeDate })
      this.props.onChange(...handleOnChangeParams(rangeDate, true, true, index === 1))

      return
    }


    utils.cloneTime(date, this.props.value[index])
    if (min && utils.compareAsc(date, min) <= 0) {
      utils.setTime(date, min)
    }
    if (max && utils.compareAsc(date, max) >= 0) {
      utils.setTime(date, max)
    }
    // if (this.state.rangeDate.filter(a => a).length !== 1) {
    //   this.setState({ rangeDate: index === 1 ? [undefined, date] : [date], hover: undefined })
    //   return
    // }

    this.setState(
      immer((draft) => {
        // const method = utils.compareAsc(draft.rangeDate[0], date) > 0 ? 'unshift' : 'push'
        draft.rangeDate = rangeDate
        draft.rangeDate[turn] = date
        draft.rangeDate[1 - index] = draft.rangeDate[1 - index] || ''
        this.changeDateSmart(draft.rangeDate)
        draft.hover = undefined
      }),
      () => {
        // only 'datetime' don not need close, 'time is up'
        this.props.onChange(...handleOnChangeParams(this.state.rangeDate, true, type !== 'datetime', index === 1))
      },
    )
  }

  fillTime(date, index) {
    const { defaultTime, format, value } = this.props
    return utils.formatDateWithDefaultTime(date, value[index], defaultTime[index], format)
  }

  handleDisabled(type, date) {
    const { disabled } = this.props
    const { rangeDate } = this.state
    if (disabled) {
      return disabled(date, type, ...rangeDate)
    }
    return false
  }

  handleQuick(quick) {
    this.setState({ rangeDate: quick.value })
    this.props.onChange(...utils.quickHandleChangeParams(quick.value, true, null, null, quick))
  }

  handleModeChange(value, index) {
    this.modeIndex = index
    setTimeout(() => {
      this.setState(immer((draft) => {
        if (index === undefined) { draft.mode = [value, value] } else {
          draft.mode[index] = value
        }
      }))
    }, 10)
  }

  renderFooter() {
    const { type, value, uiText = {} } = this.props
    const { mode } = this.state
    if (type === 'datetime') {
      return (
        <div className={datepickerClass('range-picker-footer')}>
          <Button type="link" style={{ display: 'none' }} />
          <Button type="primary" onClick={this.onConfirm} style={{ height: 30, padding: '2px 8px' }} space>{uiText.confirm}</Button>
          {
            mode[0] !== 'time' || mode[1] !== 'time'
              ? <Button type="link" onClick={this.changeTimeMode} disabled={!(value && value[0] && value[1])}>{uiText.selectTime}</Button>
              : <Button type="link" onClick={this.changeDayMode}>{uiText.selectDate}</Button>
          }
        </div>
      )
    }
    return null
  }

  render() {
    // min & max can not to child
    const { current, value, range, children, min, max, quicks, handleBlur, type, ...props } = this.props
    const { mode } = this.state
    const rangeDate = [...this.state.rangeDate]
    const rangeValue = [utils.toDateWithFormat(value[0], props.format), utils.toDateWithFormat(value[1], props.format)]
    // const hideArrow = utils.compareMonth(current[0], current[1], -1) === 0
    return (
      <div className={datepickerClass('range-picker')}>
        <div className={datepickerClass('range-picker-row')}>
          <Quick {...this.props} current={this.state.rangeDate} onChange={this.handleQuick} />
          {
             (
               <Picker
                 {...props}
                 pos="start"
                 disabled={this.handleDisabledStart}
                 index={0}
                 min={min}
                 max={max}
                 // hideArrow={hideArrow}
                 current={current[0]}
                 changeCurrent={this.changeCurrent}
                 inRange
                 range={typeof range === 'number' ? range : undefined}
                 rangeDate={rangeDate}
                 rangeTemp={rangeDate[0]}
                 onChange={this.handleFirstChange}
                 onChangeSync={this.handleChange}
                 ref={this.bindFirstPicker}
                 value={utils.toDateWithFormat(value[0], props.format)}
                 rangeValue={rangeValue}
                 showTimePicker={value.length === 2}
                 handleModeChange={this.changeFirstMode}
                 mode={mode[0]}
                 type={type}
               />
            )
          }

          {
            (
              <Picker
                {...props}
                disabled={this.handleDisabledEnd}
                index={1}
                min={type === 'time' && rangeDate[0] && !rangeDate[1] ? rangeDate[0] : min}
                max={max}
                // hideArrow={hideArrow}
                current={current[1]}
                changeCurrent={this.changeCurrent}
                inRange
                range={typeof range === 'number' ? range : undefined}
                rangeDate={rangeDate}
                rangeTemp={rangeDate[0]}
                onChange={this.handleSecondChange}
                onChangeSync={this.handleChange}
                // onDayHover={this.handleDayHover}
                ref={this.bindSecondPicker}
                value={utils.toDateWithFormat(value[1], props.format)}
                showTimePicker={value.length === 2}
                rangeValue={rangeValue}
                handleModeChange={this.changeSecondMode}
                mode={mode[1]}
                type={type}
              />
            )
          }
        </div>

        {
          this.renderFooter()
        }

      </div>
    )
  }
}

Range.propTypes = {
  current: PropTypes.array,
  disabled: PropTypes.func,
  children: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
  format: PropTypes.string,
  onChange: PropTypes.func.isRequired,
  range: PropTypes.oneOfType([PropTypes.bool, PropTypes.number]),
  value: PropTypes.array,
  type: PropTypes.string.isRequired,
  defaultTime: PropTypes.array,
  quicks: PropTypes.array,
  min: PropTypes.object,
  max: PropTypes.object,
  onHoverDateChange: PropTypes.func,
  handleBlur: PropTypes.func,
  addCloseEvents: PropTypes.func,
  uiText: PropTypes.shape(),
}

Range.defaultProps = {
  value: [],
}

export default Range
