import React from 'react'
import PropTypes from 'prop-types'
import { PureComponent } from '../public/component'
import { datepickerClass } from '../style'
import utils from '../utils'
import Year from './year'
import Month from './month'
import Day from './day'
import Time from './time'
import Quick from './quick'

class Picker extends PureComponent {
  constructor(props) {
    super(props)

    let mode
    switch (props.type) {
      case 'month':
        mode = 'month'
        break
      case 'time':
        mode = 'time'
        break
      default:
        mode = 'day'
    }

    this.state = { mode }
    const format = 'yyyy-MM-dd HH:mm:ss'
    this.defaultCurrent = utils.toDateWithFormat(
      utils.formatDateWithDefaultTime(utils.newDate(), undefined, props.defaultTime[0], format),
      format,
    )
    this.handleModeChange = this.handleModeChange.bind(this)
    this.handleEnter = this.handleMouse.bind(this, true)
    this.handleLeave = this.handleMouse.bind(this, false)
    this.handleQuick = this.handleQuick.bind(this)
  }

  handleQuick(quick) {
    const { onChange } = this.props
    onChange(...utils.quickHandleChangeParams(quick.value[0], true, null, null, quick))
  }

  handleMouse(isEnter, e) {
    // stop
    e.stopPropagation()

    const { index, handleHover, mode } = this.props
    if (mode === 'time') {
      handleHover(index, isEnter)
    }
  }

  handleModeChange(mode) {
    setTimeout(() => {
      this.setState({ mode })
    }, 10)
  }

  render() {
    const { current, index, children, mode: propsMode, handleModeChange, ...otherProps } = this.props
    let Render
    const mode = index === undefined ? this.state.mode : propsMode
    switch (mode) {
      case 'year':
        Render = Year
        break
      case 'month':
        Render = Month
        break
      case 'time':
        Render = Time
        break
      default:
        Render = Day
    }

    // only range has index prop
    if (index === undefined) {
      return (
        <div className={datepickerClass('split')}>
          <Quick
            {...otherProps}
            current={current || this.defaultCurrent}
            onChange={this.handleQuick}
          />
          <Render
            {...otherProps}
            current={current || this.defaultCurrent}
            onModeChange={this.handleModeChange}
          />
        </div>
      )
    }

    return (
      <div onMouseEnter={this.handleEnter} onMouseLeave={this.handleLeave}>
        <Render
          {...otherProps}
          index={index}
          current={current || this.defaultCurrent}
          onModeChange={handleModeChange}
        />
      </div>
    )
  }
}

Picker.propTypes = {
  current: PropTypes.oneOfType([PropTypes.array, PropTypes.object]),
  disabled: PropTypes.func,
  format: PropTypes.string,
  max: PropTypes.oneOfType([PropTypes.object, PropTypes.string]),
  min: PropTypes.oneOfType([PropTypes.object, PropTypes.string]),
  onChange: PropTypes.func.isRequired,
  value: PropTypes.object,
  type: PropTypes.string.isRequired,
  index: PropTypes.number,
  children: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
  handleHover: PropTypes.func,
  defaultTime: PropTypes.array,
  mode: PropTypes.string,
  handleModeChange: PropTypes.func,
}

export default Picker
