export function curry(f, ...args) {
  if (args.length >= f.length) {
    return f(...args)
  }

  return (...next) => curry(f.bind(f, ...args), ...next)
}

export function compose(...funs) {
  if (!funs.length) { return () => {} }
  const last = funs[funs.length - 1]
  const reset = funs.slice(0, -1)
  return (...args) => reset.reduceRight((value, f) => f(value), last(...args))
}
