/* eslint-disable jsx-a11y/no-noninteractive-tabindex   */

import React from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import immer from 'immer'
import Picker from './picker/picker'
import Range from './picker/range'
import { docSize } from './utils/dom/document'
import { getUidStr } from './utils/uid'
import { getParent } from './utils/element'
import Icon from './icon'
import Text from './Text'
import InputTitle from './InputTitle'
import { datepickerClass, inputClass, inputTitleClass } from './style'
import utils from './utils'
import List from './AnimationList'
import absoluteList from './AnimationList/AbsoluteList'
import { getRTLPosition } from './utils/strings'
import { isRTL } from './config'

const FadeList = List(['fade'], 'fast')

const OptionList = absoluteList(({ focus, ...other }) => <FadeList show={focus} {...other} />)


const getCurrentPosition = (position) => {
  if (isRTL()) {
    return getRTLPosition(position)
  }
  return position
}

class Container extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      focus: false,
      borderFocus: false,
      current: new Date(),
      position: props.position,
      picker0: false,
      picker1: false,
    }
    this.pickerId = `picker_${getUidStr()}`
    this.closeEvents = []
    this.addCloseEvents = (event) => { this.closeEvents.push(event) }
  }

  getCurrent() {
    let current
    const { defaultPickerValue, defaultRangeMonth, value } = this.props
    if (this.props.range) {
      let defaultPickerRange = defaultRangeMonth || defaultPickerValue || []
      if (!defaultPickerRange) {
        const t = new Date()
        const tt = new Date(t)
        tt.setMonth(tt.getMonth() + 1)
        defaultPickerRange = [t, tt]
      }
      current = (this.props.value || []).map((v, i) => {
        v = this.parseDate(v)
        if (utils.isInvalid(v)) v = utils.newDate(defaultPickerRange[i])
        return v
      })
      if (current.length === 0) current = [utils.newDate(defaultPickerRange[0]), utils.newDate(defaultPickerRange[1])]
      if (current[0].getMonth() === current[1].getMonth()) {
        current[1].setMonth(current[1].getMonth() + 1)
      }
    } else {
      current = this.parseDate(value || defaultPickerValue)
    }


    return current
  }

  getDefaultTime() {
    const { defaultTime } = this.props
    if (typeof defaultTime === 'string') return [defaultTime]
    if (Array.isArray(defaultTime)) return defaultTime
    return []
  }

  getFormat() {
    const { format, type } = this.props
    if (format) {
      if (type === 'week' && format.indexOf('I') > -1) return format.replace(/y/g, 'Y')

      return format
    }
    switch (type) {
      case 'date':
        return 'yyyy-MM-dd'
      case 'month':
        return 'yyyy-MM'
      case 'time':
        return 'HH:mm:ss'
      case 'week':
        return 'RRRR II'
      default:
        return 'yyyy-MM-dd HH:mm:ss'
    }
  }


  getQuick(format) {
    const { quickSelect } = this.props

    if (!Array.isArray(quickSelect)) return undefined

    return quickSelect.map((q) => {
      let invalid = false
      if (!q.value) return { name: q.name, invalid: true }
      const date = (Array.isArray(q.value)
        ? q.value : [q.value]).map(v => utils.toDateWithFormat(v, format))
      if (utils.isInvalid(date[0])) invalid = true
      if (date[1] && utils.isInvalid(date[1])) invalid = true
      if (invalid) return { name: q.name, invalid: true }
      return {
        name: q.name,
        value: date,
      }
    })
  }


  onFocus = (e) => {
    this.setState({ borderFocus: true })
    if (this.props.onFocus) {
      this.props.onFocus(e)
    }
  }

  onBlur = (e) => {
    this.setState({ borderFocus: false })
    if (this.props.onBlur) {
      this.props.onBlur(e)
    }
  }


  handleFocus = (e) => {
    if (!this.shouldFocus(e.target)) return
    this.onFocus(e)
    this.bindClickAway()
  }

  handleClickAway = (e) => {
    const onPicker = e.target === this.element || this.element.contains(e.target)
    const onAbsolutePicker = getParent(e.target, `.${datepickerClass('location')}`)
    if (!onPicker && !onAbsolutePicker) {
      if (this.props.inputable && this.textSpan) this.textSpan.blur()
      this.clearClickAway()
      this.handleToggle(false)
      this.onBlur()
    }
  }

  handleKeyDown = (e) => {
    if (e.keyCode === 13) {
      e.preventDefault()
      this.handleToggle(!this.state.focus)
    }

    // fot close the list
    if (e.keyCode === 9) {
      this.props.onBlur(e)
      // e.preventDefault()
      if (this.state.focus) this.handleToggle(false)
      else this.clearClickAway()
    }
  }


  handleClear = (e) => {
    const { clearWithUndefined } = this.props
    e.stopPropagation()
    const empty = clearWithUndefined ? undefined : ''
    const value = this.props.range ? [empty, empty] : empty
    this.props.onChange(value, () => {
      this.props.onValueBlur()
      this.handleToggle(false)
      this.element.focus()
    })
  }


  bindElement = (el) => {
    this.element = el
  }

  bindTextSpan = (el) => {
    this.textSpan = el
  }

  bindWrappedPicker = (el) => {
    this.pickerContainer = el
  }

  bindPicker = (picker) => {
    this.picker = picker
  }


  parseDate = value => utils.toDateWithFormat(value, this.getFormat(), undefined)

  handleChange = (date, change, blur, isEnd, isQuickSelect, areaType) => {
    const { onPickerChange, uiText } = this.props
    // is range only select one
    const rangeOne = this.props.range && !(date[0] && date[1])
    const format = this.getFormat()

    let value
    if (this.props.range) {
      value = date.map(v => (v
        ? utils.format(v, format, {
          weekStartsOn: uiText.startOfWeek,
        })
        : v))
    } else {
      value = utils.format(date, format, {
        weekStartsOn: uiText.startOfWeek,
      })
    }

    let callback
    if (!this.props.range) callback = blur ? this.handleBlur : undefined
    else {
      callback = this.props.autoBlur && blur && !rangeOne ? this.handleBlur : undefined
    }

    if (onPickerChange) onPickerChange(value, isQuickSelect, areaType)
    if (change) {
      if (!this.props.range || (date[0] && date[1])) {
        const newCurrent = this.dateToCurrent(date)
        if (this.props.range && newCurrent[1].getMonth() === newCurrent[0].getMonth()) {
          newCurrent[1].setMonth(newCurrent[1].getMonth() + 1)
        }
        this.setState({ current: newCurrent })
      }
      this.props.onChange(value, callback, isQuickSelect)
      if (this.props.range) { this.toggleEdit(rangeOne) }
    } else {
      this.setState({ current: this.dateToCurrent(date) }, callback)
    }
  }

  toggleEdit = () => {
  }

  handleHover = (index, isEnter) => {
    this.setState({
      [`picker${index}`]: isEnter,
    })
  }

  handleBlur = () => {
    this.handleToggle.call(this, false)
  }

  handleClick = (e) => {
    this.handleToggle.call(this, true, e)
  }

  triggerValueBlur = (cb) => {
    const { inputable } = this.props
    const { focus } = this.state
    if (cb && typeof cb === 'function') cb()
    // OnChange is not triggered when handling copy and paste
    if (inputable && focus === false) {
      this.props.onValueBlur()
    }
  }

  handleTextChange = (date, index) => {
    const format = this.getFormat()
    const val = date ? utils.format(date, format) : ''

    if (!this.props.range) {
      this.props.onChange(val, this.triggerValueBlur.bind(this, this.handleBlur))
      return
    }

    const value = [
      ...immer(this.props.value, (draft) => {
        draft[index] = val
      }),
    ]
    if (utils.compareAsc(value[0], value[1]) > 0) value.push(value.shift())
    this.props.onChange(
      value,
      this.triggerValueBlur.bind(this, () => {
        // this.setState({ current: this.getCurrent() })
      }),
    )
  }

  dateToCurrent(date) {
    const { range } = this.props
    if (!range) return date

    const { current } = this.state

    return [date[0] || current[0], date[1] || current[1]]
  }

  handleToggle(focus, e) {
    const { quickSelect } = this.props
    const hasQuickColumn = Array.isArray(quickSelect) && quickSelect.length > 0
    if (this.props.disabled === true) return

    /**
     * 
     */
    if (focus === this.state.focus) return
    if (e && focus && getParent(e.target, this.pickerContainer)) return

    // click close icon
    if (focus && e && e.target.classList.contains(datepickerClass('close'))) return
    this.setState(
      immer((state) => {
        state.focus = focus
        if (focus === true) {
          const rect = this.element.getBoundingClientRect()
          const windowHeight = docSize.height
          const windowWidth = docSize.width
          const pickerWidth = this.props.range ? 540 : 270 + (hasQuickColumn ? 120 : 0)
          if (!this.props.position) {
            if (rect.bottom + 300 > windowHeight) {
              if (rect.left + pickerWidth > windowWidth) state.position = 'right-top'
              else state.position = 'left-top'
            } else if (rect.left + pickerWidth > windowWidth) state.position = 'right-bottom'
            else state.position = 'left-bottom'
          }
          state.current = this.getCurrent()
        }
      }),
    )

    if (focus && this.picker && this.picker.resetRange) {
      this.picker.resetRange((this.props.value || []).map(this.parseDate))
    }

    if (focus === true) {
      this.firstRender = true
      // this.props.onFocus()
      this.bindClickAway()
    } else {
      this.props.onValueBlur()
      this.closeEvents.filter(handler => handler())
    }
  }


  shouldFocus(el) {
    if (el.getAttribute('data-id') === this.pickerId) return true
    if (getParent(el, `.${datepickerClass('result')}`)) return true
    return false
  }

  bindClickAway() {
    document.addEventListener('mousedown', this.handleClickAway)
  }

  clearClickAway() {
    document.removeEventListener('mousedown', this.handleClickAway)
  }

  renderText(value, placeholder, key) {
    const { inputable, formatResult, disabled } = this.props
    const date = this.parseDate(value)

    const resultFormat = formatResult || this.getFormat()

    const className = classnames(
      datepickerClass('txt', this.state[`picker${key}`] && 'text-focus'),
      utils.isInvalid(date) && inputClass('placeholder'),
    )
    return (
      <Text
        key={key}
        onTextSpanRef={this.bindTextSpan}
        // textSpan={this.textSpan}
        className={className}
        focus={this.state.focus}
        format={resultFormat}
        element={this.element}
        index={key}
        inputable={inputable}
        placeholder={placeholder}
        onChange={this.handleTextChange}
        value={utils.isInvalid(date) ? undefined : utils.format(date, resultFormat)}
        disabled={disabled === true}
      />
    )
  }

  renderResult() {
    const { range, disabled, placeholder, type, innerTitle, inputable } = this.props
    let { value, clearable } = this.props
    if (!value && range) value = []
    const isEmpty = !value || (range && value.filter(Boolean).length === 0)
    const rangeFill = (type === 'time') || (range && value.filter(Boolean).length === 2)
    if (disabled === true) clearable = false
    return (
      <div className={datepickerClass('result')}>
        <InputTitle
          className={datepickerClass('title-box')}
          contentClass={inputTitleClass('hidable')}
          innerTitle={innerTitle}
          open={!isEmpty || (inputable && this.state.focus)}
        >
          {
            range ? [
              this.renderText(rangeFill ? value[0] : undefined, placeholder[0], 0),
              <span key="-" className={datepickerClass('separate')}>
                ~
              </span>,
              this.renderText(rangeFill ? value[1] : undefined, placeholder[1], 1),
            ] : this.renderText(value, placeholder)
          }
        </InputTitle>
        <Icon className={isEmpty || !clearable ? '' : 'indecator'} name="Calendar" />
        {!isEmpty && clearable && <Icon name="CloseCircle" className="close" tag="a" onClick={this.handleClear} />}
      </div>
    )
  }

  renderWrapPicker() {
    const { focus, position } = this.state
    const { absolute, zIndex, quickSelect } = this.props
    const props = {
      absolute,
      focus,
      className: datepickerClass(
        'picker',
        'location',
        `absolute-${getCurrentPosition(position)}`,
        quickSelect && 'quick',
      ),
      zIndex,
      getRef: this.bindWrappedPicker,
    }
    // computed absolute position needed
    if (absolute) {
      props.rootClass = datepickerClass('absolute', isRTL() && 'rtl')
      props.parentElement = this.element
      props.position = position
    } else {
      props.position = getCurrentPosition(position)
    }
    return <OptionList {...props}>{this.renderPicker()}</OptionList>
  }


  renderPicker() {
    if (!this.firstRender) return undefined
    const {
      range,
      type,
      value,
      disabled,
      allowSingle,
      min,
      max,
      hourStep,
      minuteStep,
      secondStep,
      disabledTime,
      uiText,
      formatForTimeTitle,
    } = this.props
    const { focus } = this.state
    const format = this.getFormat()
    const quicks = this.getQuick(format)
    const Component = range ? Range : Picker

    return (
      <Component
        ref={this.bindPicker}
        force={type === 'time' && focus}
        uiText={uiText}
        formatForTimeTitle={formatForTimeTitle}
        defaultTime={this.getDefaultTime()}
        current={this.state.current}
        format={format}
        disabled={typeof disabled === 'function' ? disabled : undefined}
        onChange={this.handleChange}
        type={type}
        range={range}
        quicks={quicks}
        value={range ? (value || []).map(v => this.parseDate(v)) : this.parseDate(value)}
        showTimePicker={!!value}
        allowSingle={allowSingle}
        handleHover={this.handleHover}
        min={utils.toDateWithFormat(min, format)}
        max={utils.toDateWithFormat(max, format)}
        hourStep={hourStep}
        minuteStep={minuteStep}
        secondStep={secondStep}
        disabledTime={disabledTime}
        handleBlur={range ? this.handleBlur : undefined}
        addCloseEvents={this.addCloseEvents}
      >
        {this.props.children}
      </Component>
    )
  }

  render() {
    const { range, size, disabled, align, type, style, width, error, className } = this.props
    const { borderFocus } = this.state

    const newStyle = Object.assign({ width }, style)
    const rtl = isRTL()
    const isDisabled = typeof disabled === 'function' ? false : !!disabled
    const labelCLassName = classnames([
      inputClass(
        '_',
        rtl && 'rtl',
        borderFocus && !isDisabled && 'focus',
        isDisabled && 'disabled',
        size,
        newStyle.width && 'inline',
        error && 'invalid',
      ),
      datepickerClass('_', `${range ? 'r' : 'c'}-${type || 'date'}`),
      className,
    ])

    return (
      <label
        style={newStyle}
        className={labelCLassName}
      >
        <div
          tabIndex={disabled === true ? -1 : 0}
          data-id={this.pickerId}
          ref={this.bindElement}
          className={
            datepickerClass(
              'inner',
              range && 'range',
              size && `size-${size}`,
              disabled === true && 'disabled',
              align && `align-${align}`,
              getCurrentPosition(this.state.position),
            )
          }
          onFocus={this.handleFocus}
          onClick={this.handleClick}
          onKeyDown={this.handleKeyDown}
        >
          {this.renderResult()}
          {this.renderWrapPicker()}
        </div>
      </label>
    )
  }
}

Container.defaultProps = {
  clearable: true,
  placeholder: '',
  type: 'date',
  allowSingle: false,
}


Container.propTypes = {
  range: PropTypes.oneOfType([PropTypes.bool, PropTypes.number]),
  size: PropTypes.string,
  value: PropTypes.oneOfType(
    [PropTypes.number, PropTypes.string, PropTypes.object, PropTypes.array],
  ),
  placeholder: PropTypes.any,
  disabled: PropTypes.oneOfType([PropTypes.bool, PropTypes.func]),
  clearable: PropTypes.bool,
  inputable: PropTypes.bool,
  formatResult: PropTypes.oneOfType([PropTypes.string, PropTypes.func]),
  align: PropTypes.string,
  format: PropTypes.string,
  type: PropTypes.string,
  onFocus: PropTypes.func,
  onBlur: PropTypes.func,
  absolute: PropTypes.bool,
  quickSelect: PropTypes.array,
  position: PropTypes.string,
  onValueBlur: PropTypes.func,
  zIndex: PropTypes.number,
  clearWithUndefined: PropTypes.func,
  onChange: PropTypes.func,
  allowSingle: PropTypes.bool,
  style: PropTypes.shape(),
  className: PropTypes.string,
  width: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  defaultRangeMonth: PropTypes.array,
  defaultPickerValue: PropTypes.oneOfType([PropTypes.any, PropTypes.array]),
  min: PropTypes.oneOfType([PropTypes.number, PropTypes.string, PropTypes.object]),
  max: PropTypes.oneOfType([PropTypes.number, PropTypes.string, PropTypes.object]),
  hourStep: PropTypes.number,
  minuteStep: PropTypes.number,
  secondStep: PropTypes.number,
  disabledTime: PropTypes.oneOfType([PropTypes.string, PropTypes.func]),
  children: PropTypes.any,
  defaultTime: PropTypes.oneOfType([PropTypes.string, PropTypes.array]),
  onPickerChange: PropTypes.func,
  error: PropTypes.any,
  uiText: PropTypes.shape(),
  innerTitle: PropTypes.string,
  formatForTimeTitle: PropTypes.string,
  autoBlur: PropTypes.bool,
}

export default Container
